.table-container{
  padding: 0 10%;
  margin:  40px auto 0;
}
.table{
  width: 100%;
  border-collapse:collapse ;
}
.table thead{
  /* background-color:var(--primary-color);*/
  background-color: #ffffff;
 
}
.table thead tr th{
  font-size: 14px;
  font-weight: medium;
  letter-spacing: 0.35px;
 
  /* color: #ffffff;
   border: 1px solid #dee2e685;
  
  */
  opacity: 1;
  padding: 12px;
  vertical-align: top;
 
}
.table tbody tr td {
  font-size: 14px;
  letter-spacing: 0.35px;
  font-weight: normal;
  color: var(--text-color);
 background-color: #ffffff;
  /* background-color: #3c3f44;
   border: 1px solid #dee2e685;
  */
  padding: 8px;
  text-align: center;
 
}
.table .text_open{
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 0.35px;
  color: #FF1046;
}
.table tbody tr td .btn{
 
  text-decoration: none;
  line-height: 35px;
  display: inline-block;
  text-align: center;
  background-color: var(--primary-color);
  font-weight: medium;
  color: #ffffff;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  font-size: 14px;
  opacity: 1;
  width: 130px;
}
