.table-title{
  position: sticky;
  top: 0;
  
  padding: 20px 30px;
  background-color: var(--primary-color);
  color: #ffffff;
  font-size: 23px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-top-right-radius: 8px;
    border-top-left-radius: 8px;
}

.policy-container{
  display: flex;
  align-items: flex-start;
  gap: 37px;
  
}
.policy__table-box{
  width: 100%;
  max-height: 500px;
  overflow-y: auto;

}

.policy__table-box thead {
  position: sticky;
  top: 74px;
  background-color: #ffffff; /* ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏™‡∏µ‡∏û‡∏∑‡πâ‡∏ô‡∏´‡∏•‡∏±‡∏á‡πÉ‡∏´‡πâ‡∏´‡∏±‡∏ß‡∏ï‡∏≤‡∏£‡∏≤‡∏á */
