.items-per-page {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  padding: 18px 12px;
  font-size: 14px;
  background:  white;
}

.items-per-page label {
  margin-right: 10px;
  font-weight: bold;
}

.items-per-page select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: border-color 0.3s;
}

.items-per-page select:hover {
  border-color: #888; /* ‡πÄ‡∏õ‡∏•‡∏µ‡πà‡∏¢‡∏ô‡∏™‡∏µ‡πÄ‡∏°‡∏∑‡πà‡∏≠‡πÄ‡∏°‡∏≤‡∏™‡πå‡∏ß‡∏≤‡∏á */
}

