@media  (max-width:1055px) {
  .table thead tr th{
    font-size: 12px;
  }
  .table tbody tr td{
    font-size: 12px;
  }
  .table tbody tr td .btn{
    font-size: 12px;
  }
}
@media  (max-width:550px) {
  .items-per-page{
display: grid;
gap: 10px
;
  }
  .search-bar input{

  }
}

@media  (max-width:768px) {
  .table thead{
    display: none;
  }
  .table, .table tbody,.table tr,.table td{
    display: block;
  
  }
  .table tr{
    margin-bottom: 15px;
  }
  .table tbody tr td{
    text-align: right;
  padding-left: 50%;
  position: relative;
  }
  .table td::before{
    content: attr(data-label);
    position: absolute;
    left: 0;
    width: 50%;
    padding-left: 30px;
    font-weight: 600;
    font-size: 14px;
    text-align: left;
    /*    color: #ffffff;*/

  }
  .policy__table-box{
    max-height: none;
    overflow-y: hidden;
  }



  .table td .order-icon {
    position: absolute;
    left: 10px; /* ‡∏£‡∏∞‡∏¢‡∏∞‡∏´‡πà‡∏≤‡∏á‡∏à‡∏≤‡∏Å‡∏Ç‡∏≠‡∏ö‡∏ã‡πâ‡∏≤‡∏¢ */
    top: 50%; /* ‡πÅ‡∏ô‡∏ß‡∏ï‡∏±‡πâ‡∏á‡∏Å‡∏•‡∏≤‡∏á */
    transform: translateY(-50%); /* ‡∏õ‡∏£‡∏±‡∏ö‡πÉ‡∏´‡πâ‡πÑ‡∏≠‡∏Ñ‡∏≠‡∏ô‡∏≠‡∏¢‡∏π‡πà‡∏Å‡∏•‡∏≤‡∏á */
  }
  .table td .order-icon{
    display: block;
  }
}